.area-info {
    display: none;
    position: absolute;
    top: 0;
    left: 0;

    .info {
        position: fixed;
        width: 40%;
        max-width: 720px;
        height: calc(100% - 48px);
        padding: 36px 32px;
        top: 24px;
        right: -50%;
        z-index: -10;
        transition: 0.7s ease-in-out;
        opacity: 0;
        background: $cl-white;
        border-radius: 20px;
        overflow: scroll;
        box-shadow: 4px 12px 24px rgba(0, 0, 0, 0.12);
        cursor: auto;

        -ms-overflow-style: none;
         &::-webkit-scrollbar{
           display:none;
         }

        .close {
            position: absolute;
            top: 36px;
            right: 32px;
            width: 28px;
            height: 28px;
            background: #eee;
            border-radius: 50%;
            cursor: pointer;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    background: $cl-main;
                    opacity: 0.5;
                }
            }

            .bar {
                display: block;
                position: absolute;
                top: 14px;
                left: 5px;
                width: 17px;
                height: 1px;
                background: $cl-main;
                transition: 0.3s ease;

                &:first-child {
                    transform : rotate(45deg)
                }

                &:last-child {
                    transform: rotate(135deg);
                }
            }
        }

        .expander {
            position: absolute;
            top: 76px;
            right: 32px;
            width: 28px;
            height: 28px;
            cursor: pointer;
            border-radius: 50%;
            background: #eee;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    background: #A9A4A4;
                    &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #A9A4A4;}
                    &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                    &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                    &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                    &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                    &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                    &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                    &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                    &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                }
            }

            .bar {
                position: absolute;
                display: block;
                width: 1px;
                height: 1px;
                background: #685F5F;
                transition: 0.3s ease;
                border-radius: 1px;

                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
            }
        }

        .reduction {
            .bar {
                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                
                &:hover {
                    .bar {
                        &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid #685F5F;}
                        &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                        &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                        &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                        &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                        &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                        &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                        &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                        &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
                    }
                }
            }
        }

        .info-content {
            .copy-sub-heading {
                width: 100%;
                padding-bottom: 12px;
            }
            .copy-body-large {
                padding-bottom: 2px;
            }

            .info-header-section {
                width: calc(100% - 60px);
                padding-bottom: 24px;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .copy-heading {
                    padding-right: 16px;
                }

                .info-illust {
                    height: 140px;
                    width: max-content;
                }
            }

            .info-spec {
                width: 100%;
                padding: 24px 0;

                .info-spec-wrap {
                    display: flex;
                    gap: 16px;

                    .info-spec-block {
                        padding: 12px;
                        width: 33.3333%;
                        text-align: left;
                        border-radius: 4px;
                        background: #E9E7DF;
                        .copy-body-large {
                            .copy-body-small{
                                padding-left: 6px;
                                }
                        }

                        .copy-sub-heading {
                            padding: 0;
                        }
                    }
                }
            }

            .info-user {
                width: 100%;
                .user-card {
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    align-items: center;
                    gap: 8px;
                    padding: 12px 6px;
                    background: #F2F2F2;
                    border-radius: 6px;
                    text-align: center;
                    .user-image {
                        height: 80px;
                    }
                    .copy-body-large,
                    .copy-body-small {
                        width: 100%;
                    }
                }

                .focused-user {
                    padding: 24px 0;
                    .focused-user-card-list {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 12px;
                    }
                    .focused-user-insight {
                        display: none;
                        .insight {
                            padding: 8px 0;
                            .copy-body-small {
                                position: relative;
                                margin-left : 10px;

                                i {
                                    display: block;
                                    position: absolute;
                                    top: 4px;
                                    left: -10px;
                                    width: 4px;
                                    height: 4px;
                                    background: $cl-body-text;
                                    border-radius: 2px;
                                }
                            }
                        }
                    }
                }

                .related-user {
                    padding: 24px 0;
                    .related-user-card-list {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 12px;
                    }
                }
            }

            .info-area-context {
                width: 100%;
                padding: 24px 0;
                .area-context-content {
                    display: flex;
                    flex-direction: column;
                    gap: 16px;
                    .area-context-content-wrap {
                        display: flex;
                        gap: 16px;
                        .context-content {
                            width: 33.3333%;
                            padding: 12px;
                            width: 33.3333%;
                            text-align: left;
                            border-radius: 4px;
                            background: #E9E7DF;
                            .copy-body-large {
                                .copy-body-small {
                                    padding-left: 6px;
                                }
                            }
                            
                            .context-content-list {
                                display: flex;
                                flex-direction: column;
                                gap: 8px;
                                margin-top: 4px;

                                .copy-body-small {
                                    position: relative;
                                    margin-left: 10px;
    
                                    i {
                                        display: block;
                                        position: absolute;
                                        top: 4px;
                                        left: -10px;
                                        width: 4px;
                                        height: 4px;
                                        background: $cl-body-text;
                                        border-radius: 2px;
                                    }
                                }   
                            }
                        }
                    }
                    .context-image-wrap {
                        img {
                            border-radius: 6px;
                        }
                    }
                }
            }

            .info-fac-serve {
                width: 100%;
                padding: 24px 0;
                
                .info-fac-serve-container {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                    .info-fac-serve-list {
                        display: flex;
                        flex-wrap: wrap;
                        gap: 6px;
    
                        li {
                            padding: 6px 12px;
                            color: #685F5F;
                            font-size: 14px;
                            line-height: 18px;
                            cursor: pointer;
                            background: #E9E7DF;
                            border-radius: 4px;
                        }
    
                        .selected {
                            color: #FFF;
                            background:#685F5F;
                        }
                    }

                    .info-fac-serve-desc {
                        .info-desc,
                        .info-career {
                            padding: 12px 0;
                            white-space: pre-wrap;
                            .career {
                                width: max-content;
                                text-decoration: underline;
                                margin-right: 4px;
                                color: $cl-body-text;
                                opacity: 0.7;
                                transition: 0.3s ease;

                                &:hover {
                                    opacity: 1;
                                }
                            }
                        }
                        
                        .hide-desc,
                        .hide-career {
                            display: none;
                        }
                    }

                    .info-fac-serve-wrap {
                        .info-fac-serve-content {
                            display: block;
                            width: 100%;
    
                            .image-wrap-slide {
                                position: relative;
                                padding: 4px 0;
                                height: 380px;
                                overflow: hidden;
                                border-radius: 6px;

                                &:hover {
                                    .button {
                                        opacity: 1;
                                    }
                                }

                                .button {
                                    position: absolute;
                                    width: 28px;
                                    height: 28px;
                                    right: 24px;
                                    z-index: 15;
                                    background: rgba(255,255,255,0.7);
                                    border-radius: 50%;
                                    cursor: pointer;
                                    opacity: 0;
                                    transition: 0.3s ease;

                                    &:hover {
                                        background: $cl-sub-background;
                                    }

                                    img {
                                        position: absolute;
                                        left: 7px;
                                        width: 14px;
                                        height: 14px;
                                    }
                                }

                                .button-pre {
                                    top: 24px;
                                    img {
                                        top: 7px;
                                    }
                                }

                                .button-next {
                                    bottom: 24px;
                                    img {
                                        top : 8px;
                                    }
                                }

                                .hide-button {
                                    display: none;
                                }

                                .dot-wrap {
                                    display: flex;
                                    height: 100%;
                                    flex-direction: column;
                                    justify-content: center;
                                    gap: 6px;
                                    position: absolute;
                                    right: 36px;
                                    z-index: 10;
                                    .dot {
                                        width: 4px;
                                        height: 18px;
                                        border-radius: 2px;
                                        background: rgba(255,255,255,0.4);
                                        cursor: pointer;
                                        transition: 0.5s ease;
                                        &:hover {
                                            background: $cl-sub-background;
                                        }
                                    }

                                    .expand-dot {
                                        height: 48px;
                                        background: #fff;
                                    }
                                }

                                .hide-dot-wrap {
                                    display: none;
                                }

                                .image-wrap {
                                    width: 100%;
                                    display: flex;
                                    flex-direction: column;
                                    position: absolute;
                                    transition: 0.8s ease;
                                    .image {
                                        width: 100%;
                                        height: 380px;
                                        transition: 0.3s ease;
                                        transform-origin: center center;
                                        opacity: 0.8;
                                        margin-bottom: 12px;
                                        img {
                                            width: 100%;
                                            height: 100%;
                                            object-fit: cover;
                                            transition: 0.3s ease;
                                        }

                                        &:last-child {
                                            margin-bottom: 0;
                                        }
                                    }

                                    .show-image {
                                        opacity: 1;
                                        transform: scale(1);
                                    }
                                }
                            }
    
                            .info-fac-serve-content-wrap {
                                padding-top: 24px;
                                .copy-body-small {
                                    position: relative;
                                    margin-left: 10px;
                                    
                                    i {
                                        display: block;
                                        position: absolute;
                                        top: 4px;
                                        left: -10px;
                                        width: 4px;
                                        height: 4px;
                                        background: $cl-body-text;
                                        border-radius: 2px;
                                    }
                                }
                            }
        
                            .image-front {
                                opacity: 1;
                            }
        
                            .image-back {
                                opacity: 0;
                            }
                        }
        
                        .hide-content {
                            display: none;
                        }
                    }
                }
            }
        }
    }

    .info-showing {
        right: 48px;
        opacity: 1;
        z-index: 900;
        transition: 0.7s ease-in-out;
        transition-delay: 0.3s;
    }

    .info-expanding {
        top: 0px;
        right: 0px;
        width: 100%;
        max-width: 100%;
        height: 100%;
        border-radius: 0;
        // width: calc(100% - 96px);
        // height: calc(100% - 48px);
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(8px);

        .info-content {
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            justify-content: space-between;
            .info-spec {
                padding: 0;
                .info-spec-wrap {
                    gap: 10px;
                    .info-spec-block {
                        width: max-content;
                        background: none;
                        padding: 0 24px 12px 12px;
                    }
                }
            }

            .info-user {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 22%;
                gap: 16px;
                .focused-user {
                    height: 50%;
                    padding: 22px 16px;
                    background: $cl-sub-background;
                    border-radius: 8px;

                    .focused-user-insight {
                        width: 100%;
                        margin-top: 24px;
                        display: flex;
                        flex-wrap: wrap;
                        .insight {
                            width: 50%;
                            padding: 24px 12px;
                        }
                    }
                }
                .related-user {
                    height: 50%;
                    padding: 22px 16px;
                    background: $cl-sub-background;
                    border-radius: 8px;
                }
            }

            .info-area-context {
                padding: 22px 16px;
                width: 42%;
                background: $cl-sub-background;
                border-radius: 8px;
                .area-context-content {
                    .area-context-content-wrap {
                        .context-content {
                            background:$cl-white;
                        }
                    }
                }
            }
            
            .info-fac-serve {
                padding: 22px 16px;
                width: 32%;
                background: $cl-sub-background;
                border-radius: 8px;
                .info-fac-serve-container {
                    .info-fac-serve-list {
                        li {
                            background: $cl-white;
                        }
                    }
                }
            }
        }
    }
}

.show-area-info {
    display: block;
}