.teaser {
    opacity: 1;
    position: absolute;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background: $cl-background;
    transition: 0.3s ease;
    overflow: auto;
    cursor: auto;

    .teaser-container {
        position: relative;
        width: 100%;
        max-width: 1440px;
        margin: 0 auto;
        padding: 36px 24px;

        .close {
            position: absolute;
            top: 36px;
            right: 48px;
            cursor: pointer;
        }

        .content {
            display: flex;
            flex-direction: column;
            gap: 24px;
            padding: 48px 0;
            
            .block {
            }

            .image-content-block {
                display: flex;
                gap: 24px;
                .image-title-wrap {
                    .title-list-selected {
                        color: black;
                    }

                    .title-list {
                        cursor: pointer;
                    }
                }
                .image-block {
                    width: 50%;
                    .image-wrap {
                        display: none;
                        .images {
                            width: 100%;
                            display: none;
                        }
                        .show-images {
                            display: block;
                        }
                    }
                    .show-image-wrap {
                        display: block;
                    }

                    .numbering-wrap {
                        display: none;
                    }
                    .show-numbering-wrap {
                        display: block;
                    }
                }
                .image-desc-wrap {
                    width: 50%;
                    .desc {
                        display: none;
                    }
                    .show-desc {
                        display: block;
                    }
                }
            }
        }
    }
}

.teaser-hide {
    opacity: 0;
    z-index: -50;
    overflow: hidden;
}