.s-hills-pin-wrap {
    opacity: 0;
    position: absolute;
    top: calc(50% - 450px);
    left: calc(50% - 747px);
    width: 1000px;
    height: 900px;
    z-index: 10;
    transition: 1s ease;

    .s-hills-pin {
        position: absolute;
        width: 48px;
        height: 48px;

        .hit {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
            width: 100%;
            height: 100%;
            cursor: pointer;

            &:hover ~ .pin {
                background: #A9A4A4;
                .pin-head {
                    color: #191919;
                }
            }
        }

        .pin {
            position: absolute;
            top: 14px;
            left: 14px;
            width: 20px;
            height: 20px;
            border-radius: 14px;
            background: #191919;
            transition: 0.5s ease;
            
            .pin-head {
                position: absolute;
                top: 1px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 10px;
                color: $cl-background;
            }
        }

        .pin-headline {
            opacity: 0;
            display: flex;
            align-items: center;
            position: absolute;
            transition: 0.3s ease;
            .pin-title {
                width: max-content;
                padding: 6px 0;
                font-size: 10px;
                line-height: 14px;
            }

            .pin-icon {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 48px;
                height: 48px;
                border: 1px solid $cl-black;
                border-radius: 50%;
                background: $cl-background;

                img {
                    width: 36px;
                    height: 36px;
                }
            }

            .pin-line {
                width: 0.2px;
                height: 200px;
                background: #191919;
            }
        }

        .hide-pin-headline {
            opacity: 0;
        }


        // Chapel
        &:nth-child(1) { top: calc(150px); left: calc(400px); 
            .pin-headline {
                bottom: 34px;
                flex-direction: column;
                .pin-line {
                    height: 64px;
                }
            }
        }
        // bakery-cafe
        &:nth-child(2) { top: calc(280px); left: calc(420px);
            .pin-headline {
                left: -22px;
                top: 34px;
                flex-direction: column-reverse;
            }
        }
        // treehouse
        &:nth-child(3) { top: calc(320px); left: calc(510px); 
            .pin-headline { 
                left: -1px;
                top: 34px;
                flex-direction: column-reverse;
            }
        }
    }

    .s-hills-pin-selected {
        .pin {
            background: #A9A4A4;
                .pin-head {
                    color: #191919;
                }
        }
    }
}

.show-s-hills-pin-wrap {
    opacity: 1;
}