.splash {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 900;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: #F0EBE0;
    transition: 2s ease-out;
    transition-delay: 2.4s;

    .copy-heading {
        text-align: center;
        width: max-content;
        margin-bottom: 24px;
    }

    p {
        font-size: 12px;
        font-style: italic;
        font-weight: bold;
        line-height: 14px;
        color: #685F5F;
    }
}

.disappear {
    opacity: 0;
    z-index: -1;
}