.s-fin-pin-wrap{
    opacity: 0;
    position: absolute;
    top: calc(50% - 450px);
    left: calc(50% - 253px);
    width: 1000px;
    height: 900px;
    z-index: 10;
    transition: 1s ease;

    .s-fin-pin {
        position: absolute;
        width: 48px;
        height: 48px;

        .hit {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 10;
            width: 100%;
            height: 100%;
            cursor: pointer;

            &:hover ~ .pin {
                background: #A9A4A4;
                .pin-head {
                    color: #191919;
                }
            }
        }

        .pin {
            position: absolute;
            top: 14px;
            left: 14px;
            width: 20px;
            height: 20px;
            border-radius: 14px;
            background: #191919;
            transition: 0.5s ease;
            
            .pin-head {
                position: absolute;
                top: 1px;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 10px;
                color: $cl-background;
            }
        }

        .pin-headline {
            opacity: 0;
            display: flex;
            align-items: center;
            position: absolute;
            transition: 0.3s ease;
            .pin-title {
                bottom : 2px;
                width: max-content;
                padding: 0 6px;
                font-size: 10px;
                line-height: 14px;
            }

            .pin-icon {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 48px;
                height: 48px;
                border: 1px solid $cl-black;
                border-radius: 50%;
                background: $cl-background;

                img {
                    width: 36px;
                    height: 36px;
                }
            }

            .pin-line {
                height: 0.2px;
                background: #191919;
            }
        }

        .hide-pin-headline {
            opacity: 0;
        }


        // premium area
        &:nth-child(1) { top: calc(50px); left: calc(744px); 
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                .pin-line {width: calc(1000px - 714px);}
            }
        }

        // centre of forest & sunset bridge point
        &:nth-child(2) { top: calc(190px);left: calc(766px);
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                    .pin-line {width: calc(1000px - 736px);}
            }
        }
        // sauna & restaurant
        &:nth-child(3) { top: calc(246px); left: calc(640px);
            .pin-headline {right: 34px;
                .pin-line {width: 440px;}
            }
        }

        // bbq deck
        &:nth-child(4) { top: calc(300PX); left: calc(770PX); 
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                .pin-line {width: calc(1000px - 740px);}
            }
        }

        // cocktail & organic tea garden
        &:nth-child(5) { top: calc(360px); left: calc(640px); 
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                .pin-line {width: calc(1000px - 610px);}
            }
        }

        // play garden
        &:nth-child(6) { top: calc(374px); left: calc(567PX); 
            .pin-headline {
                right: 34px;
                .pin-line {width: 367px;}
            }
        }

        // main centre
        &:nth-child(7) { top: calc(430px); left: calc(590PX); 
            .pin-headline {right: 34px;
                .pin-line {width: 390px;}
            }
        }

        // meditation garden
        &:nth-child(8) { top: calc(490px); left: calc(536px); 
            .pin-headline { right: 34px;
                .pin-line {width: 336px;}
            }
        }

        // cocktail & organic tea garden
        &:nth-child(9) { top: calc(440px); left: calc(860px);
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                .pin-line {width: calc(1000px - 830px);}
            }
        }
        
        // pet area
        &:nth-child(10) { top: calc(700px); left: calc(620px); 
            .pin-headline {
                left: 34px;
                flex-direction: row-reverse;
                .pin-line {width: calc(1000px - 590px);}
            }
        }
    }

    .s-fin-pin-selected {
        .pin {
            background: #A9A4A4;
                .pin-head {
                    color: #191919;
                }
        }
    }
}

