


$cl-main : #685F5F;
$cl-background : #F0EBE0;
$cl-sub-background : #E9E7DF;
$cl-body-text : #676767;

$cl-white : #FFFFFF;
$cl-black : #191919;



@mixin font-heading {
    display: flex;
    align-items: flex-end;
    font-size: 32px;
    line-height: 42px;
    font-weight: bold;
    color: $cl-main;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-sub-heading {
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 36px;
    font-weight: bold;
    color: $cl-main;
    font-family: Helvetica-Bold, sans-serif;
}

@mixin font-body-large {
    font-size: 16px;
    font-weight: bold;
    line-height: 22px;
    font-family: Helvetica-Regular, sans-serif;
    color: $cl-main;
}

@mixin font-body-regular {
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
    color: $cl-body-text;
}

@mixin font-body-small {
    display: inline-block;
    font-size: 12px;
    line-height: 16px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
    color: $cl-body-text;
}

@mixin font-caption {
    display: inline-block;
    font-size: 10px;
    line-height: 14px;
    font-weight: normal;
    font-family: Helvetica-Regular, sans-serif;
    color: $cl-body-text;
}