@import "./splash.scss";
@import "./teaser.scss";
@import "./sono-fin.scss";
@import "./sono-hills.scss";
@import "./area-information.scss";
@import "./map-swipe.scss";
@import "./modular-info.scss";
@import "./area-choice.scss";

.map-wrapper {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    cursor: grab;

    .map-container {
        width: 100%;
        height: 100%;
        position : relative;

        .sono {
            transition: 1s ease;
        
            .background-map {
                position: absolute;
                top: calc(50% - 450px);
                left : calc(50% - 747px);
                width: 1495px;
                height: 900px;
                overflow: hidden;
                transition: 1s ease;
        
                img {
                    position: absolute;
                    left: 0;
                    width: 1495px;
                    height: 900px;
                    transition: 1s ease;
                }
            }
        }
        
        .sono-fin {
            .s-fin-pin-wrap {
                width: 1000px;
                left: calc(50% - 500px);
                opacity: 1;
                z-index: 10;
                overflow: visible;
                .s-fin-pin {
                    .pin-headline {
                        opacity: 1;
                    }
                }
            }

            .s-fin-fac-pin-wrap {
                width: 1000px;
                left: calc(50% - 500px);
                opacity: 1;
                z-index: 9;
                overflow: visible;
                .s-fin-fac-pin {
                    .pin-headline {
                        opacity: 1;
                    }
                }
            }

            .s-hills-pin-wrap {
                width: 0px;
                overflow: hidden;
            }

            .background-map {
                width: 1000px;
                top: calc(50% - 450px);
                left : calc(50% - 500px);
                img {
                    left: -495px;
                }
            }
        }

        .sono-hills {
            .s-hills-pin-wrap {
                width: 1000px;
                left: calc(50% - 500px);
                opacity: 1;
                overflow: visible;
                .s-hills-pin {
                    .pin-headline {
                        opacity: 1;
                    }
                }
            }

            .s-fin-pin-wrap,
            .s-fin-fac-pin-wrap {
                width: 0px;
                overflow: hidden;
            }

            .background-map {
                width: 1000px;
                top: calc(50% - 450px);
                left : calc(50% - 500px);
                img {
                    left: 0;
                }
            }
        }

        .layer {
            position: absolute;
            top: calc(50% - 450px);
            left: calc(50% - 600px);
            z-index: 5;
            width: 1200px;
            height: 900px;
            background: none;
            transition: 1s ease;
            opacity: 1;
        }

        .layer-hide {
            opacity: 0;
            transition: 0.5s ease;
        }

        .layer-left {
            background: linear-gradient(270deg, rgba(240, 235, 224, 0) 61.56%, #F0EBE0 70.99%);
            transition: 1s ease;
        }

        .layer-right {
            background: linear-gradient(270deg, #F0EBE0 0%, #F0EBE0 8.34%, rgba(240, 235, 224, 0) 20.18%);
            transition: 1s ease;
        }
    }
}